code,
* {
  font-family: Arial, Helvetica, sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 10px;
}

.signforms {
  border: 1px solid black;
  width: fit-content;
  display: flex;
  margin: auto;
  margin-top: 10%;
  padding: 3rem;
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.right {
  margin-left: 2rem;
  border-left: 1px solid red;
  padding-left: 2rem;
}

h2 {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 3rem;
}

.signforms div,
.signforms input,
.signforms button {
  font-size: 2rem;
}

.signforms button {
  width: 100%;
}
